import { createSlice } from "@reduxjs/toolkit";

export const navSlice = createSlice({
  name: "nav",
  initialState: [
    { id: 0, name: "", to: "/" },
    { id: 1, name: "", to: "/news" },
    { id: 2, name: " ", to: "/about" },
    { id: 3, name: "", to: "/documents" },
    { id: 4, name: "", to: "/program" },
    { id: 5, name: " ", to: "/projects" },
    { id: 6, name: "", to: "/gallery" },
    { id: 7, name: "", to: "/ads" },
    { id: 8, name: "", to: "/reviews" },
  ],
  reducers: {
    reducer: (state) => state,
  },
});

export const { reducer } = navSlice.actions;

export default navSlice.reducer;
